  list-style: none;
}

.list-text{
 padding: 5px 0 8px 0;
}

input[type="radio"]{
  -webkit-appearance: none;
          appearance: none;
  border: 1px solid #1f1f1f;
  width: 15px;
  height: 15px;
  content: none;
  outline: none;
  margin: 0;
  background-color: #ffffff;
  border-radius: 2px; 
  /* box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px; */
}

input[type="radio"]:checked {
 -webkit-appearance: none;
         appearance: none;
 outline: none;
 padding: 0;
 content: none;
 border: none;
}

input[type="radio"]:checked::before{
 position: absolute;
 background-color: #f46430; 
 color: #FFFFFF !important;
 content: "\00A0\2713\00A0" !important;
 border: 1px solid #1f1f1f;
 border-radius: 2px; 
 font-weight: bolder;
 font-size: 9px;
 height: 15px;
 width: 15px;
}

input[type="radio"]:checked::after{
  -webkit-appearance: none;
          appearance: none;
  border: 1px solid #1f1f1f;
  width: 15px;
  height: 15px;
  content: none;
  outline: none;
  margin: 0;
  background-color: #ffffff;
  border-radius: 2px; 
 }




