.VideoImageCardContainer {
    width: 100%;
    margin-left: 0px;
    display: flex;
    flex-direction: column;
}

/* Center website */

.card_title {
    font-size: 50px;
    word-break: break-all;
}

.VideoImageCardRow {
    /* margin-top: 23px; */
    width: 100%;
}

/* Add padding BETWEEN each column */
.VideoImageCardRow,
.VideoImageCardRow>.VideoImageCardColumn {
    padding-bottom: 30px;
}

.VideoImageCardRow,
.VideoImageCardRow>.VideoImageCardColumn {
    padding-bottom: 30px;
}

/* Create four equal columns that floats next to each other */
.VideoImageCardColumn {
    float: left;
    width: 33.33%;
}

/* Clear floats after rows */
.VideoImageCardRow:after {
    content: "";
    display: table;
    clear: both;
}

/* Content */
.VideoImageCardContent {
    background-color: white;
    /* padding: 15px; */
    position: relative;
    background: #FFFFFF;
    border: 1px solid rgba(31, 31, 31, 0.2);
    box-sizing: border-box;
    border-radius: 10px;
    width: 400px;
    height: 250px;
    margin-right: 83px;
}

.VideoImageCardContent-add {
    background-color: white;
    padding: 15px;
    background: #F5F5F5;
    border: 1px dashed #696969;
    box-sizing: border-box;
    border-radius: 10px;
    width: 400px;
    height: 250px;
    display: flex;
    align-items: center;
    margin-right: 83px;
    position: relative;
}

.VideoImageCardContentContainer {
    padding: 20px 0 20px 15px;
}

.card_date {
    font-family: 'Inter';
    font-style: normal;
    font-weight: 400;
    font-size: 14px;
    line-height: 17px;
    color: #696969;
}

.card_author {
    font-family: 'Inter';
    font-style: normal;
    font-weight: 400;
    font-size: 16px;
    line-height: 19px;
    color: #696969;
    padding-top: 4px;
    padding-bottom: 4px;
}

.card_title {
    font-family: 'Inter';
    font-style: normal;
    font-weight: 600;
    font-size: 18px;
    line-height: 22px;
    color: #1F1F1F;
    padding-top: 2px;
    padding-bottom: 4px;
}

.card_image {
    height: 13.94px;
    width: 12.15px;
    margin-right: 5px;
    margin-bottom: 2px;
}



.remark {
    display: flex;
    flex-direction: row;
    margin-top: 14px;
    font-family: 'Inter';
    font-style: normal;
    font-weight: 400;
    font-size: 14px;
    line-height: 17px;
    color: #1F1F1F;
}

.remark_item {
    padding-right: 10px;
}

.CardCrossImage {
    position: absolute;
    width: 17px;
    height: 17px;
    top: 9px;
    right: 9px;
    content: '';
}

.CardCross {
    height: 35px;
    width: 35px;
    position: absolute;
    z-index: 2;
    right: 15px;
    top: 20px;
    background-color: #ffffff;
    border-radius: 50%;
}


.checkbox_input {
    height: 20px;
    width: 20px;
    position: absolute;
    z-index: 2;
    border-radius: 5px;
    left: 15px;
    top: 20px;
    background-color: #ffffff;
}



.container_input {
    display: block;
    position: relative;
    padding-left: 35px;
    margin-bottom: 12px;
    cursor: pointer;
    font-size: 20px;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
}


.container_input input {
    position: absolute;
    opacity: 0;
    cursor: pointer;
    height: 0;
    width: 0;
}


.checkmark {
    position: absolute;
    top: 0;
    left: 0;
    height: 20px;
    width: 20px;
    border-radius: 4px;
    background-color: #ffffff;
}


.container_input:hover input~.checkmark {
    background-color: #F5F5F5;
}


.container_input input:checked~.checkmark {
    background-color: #f46430;
}


.checkmark:after {
    content: "";
    position: absolute;
    display: none;
}


.container_input input:checked~.checkmark:after {
    display: block;
}


.container_input .checkmark:after {
    left: 7px;
    top: 3px;
    width: 6px;
    height: 12px;
    border: solid #ffffff;
    border-width: 0 3px 3px 0;
    -webkit-transform: rotate(45deg);
    -ms-transform: rotate(45deg);
    transform: rotate(45deg);
}



.project_add {
    height: 50px;
    width: 50px;
    position: absolute;
    z-index: 2;
    top: 200px;
    left: 174px;
}

.AddNewProject {
    font-family: 'Inter';
    font-style: normal;
    font-weight: 600;
    font-size: 20px;
    line-height: 24px;
    color: #696969;
}

.ButtonCard {
    position: absolute;
    text-decoration: none;
    background-color: none;
    background: none;
    border: none;
    font-family: inter;
    position: absolute;
    transform: translate(-50%, -50%);
    top: 50%;
    left: 50%;
}

.ImageCover{
    position: absolute;
    transform: translate(-50%, -50%);
    top: 50%;
    left: 50%;
}

.ButtonCardDelete {
    position: absolute;
    text-decoration: none;
    background-color: none;
    background: none;
    border: none;
    font-family: inter;
    right: 0;
    top: 0;
}


@media only screen and (max-width: 1500px) {

    .VideoImageCardColumn {
        float: left;
        width: 50%;
    }

}