  /* Font */
  @import url('https://fonts.googleapis.com/css2?family=Inter:wght@600;400&display=swap');

/* Design */
*,
*::before,
*::after {
  box-sizing: border-box;
}


.main-container {
    max-width: 1450px;
    margin: 0px;
    display: flex;
    flex-wrap: wrap;
}

.add-icon {
    width: 400px;
    height: 393px;
    margin-right: 60px;
    border: 2px dashed black;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.addicon-image {
    width: 37px;
    height: 37px;
}

.card {
    border: 1px solid rgba(31, 31, 31, 0.2);
    border-radius: 10px;
    width: 400px;
    height: 393px;
    margin-right: 60px;
    margin-bottom: 60px;
}
.card-image img{
    width: 400px;
    border-radius: 10px;
}

.card-content {
    padding-left: 15px;
}

.card-content h2{
    font-size: 14px;
    color: #696969;
    font-family: 'Inter';
    font-style: normal;
    line-height: 17px;
}

.card-content p {
    font-size: 18px;
    font-family: 'Inter';
    font-style: normal;
    font-weight: 600;
    line-height: 22px;
    color: #1F1F1F;
}

.card-content ul {
    display: flex;
    margin-left: 0px;
    padding: 0;
}
.card-content ul li {
    font-size: 14px;
    line-height: 17px;
    display: flex;
    align-items: center;
    color: #1F1F1F;
    font-family: 'Inter';
    font-style: normal;
    list-style-type: none;
}


.single-mediaicon {
    list-style-type: none;;
}
.file-upload{
    background: #F5F5F5;
